*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Noto Sans JP", sans-serif;
  line-height: 1.6;
}

section {
  padding: 7rem 0;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  width: 85%;
  max-width: 65em;
  margin: 0 auto;
}

.split {
  display: flex;
  gap: 1em;
  flex-wrap: wrap;
  justify-content: center;

  & > * {
    flex-basis: 30%;
    min-width: 15em;
  }
}

.spacing > * + * {
  margin-top: var(--spacer, 2rem);
}

.btn {
  display: inline-block;
  text-decoration: none;
  color: var(--clr-text, #fff);
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1.125rem;
  border-radius: 0.25em;

  padding: 0.5em 1.25em;
  background: var(--clr-accent, lightblue);

  transition: transform 0.25s ease-in-out, opacity 0.25s linear;

  &:hover,
  &:focus {
    transform: scale(1.1);
    opacity: 0.9;
  }
}

.hero {
  color: #fff;
  text-align: center;
  padding: 15rem 0;
  background: #222;

  @supports (background-blend-mode: multiply) {
    background: url(../img/shoe-3.png), radial-gradient(#444, #111);
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: center center;
  }

  //   background-size: 10%;
}

.main-title {
  font-size: 4rem;
  font-size: clamp(3rem, calc(5vw + 1rem), 4.5rem);
  line-height: 1;
  text-transform: uppercase;
}

.section-title {
  text-align: center;
  font-size: clamp(2.5rem, calc(5vw + 1rem), 4rem);
  line-height: 1;
  color: #17353d;
  margin-bottom: 5rem;
}

.featured {
  background: #eee;

  &__item {
    display: block;
    position: relative;
    transform: scale(0.85);

    transition: transform 0.25s ease-in-out;

    text-decoration: none;
    color: #333;
    text-align: center;
    line-height: 1.2;

    &:hover,
    &:focus {
      transform: scale(1);
      .featured__details {
        opacity: 1;
        text-shadow: 0 0 0.25em rgba(#185568, 1);
      }
    }

    &::after {
      content: "";
      position: absolute;
      top: 10%;
      left: 10%;
      padding: 75% 75% 0 0;
      background: #1a6f85;
      border-radius: 50%;
      z-index: -1;
    }
  }

  &__details {
    opacity: 0;
    transition: opacity 0.25s ease-in-out;

    & .price {
      display: block;
      font-weight: 700;
      font-size: 2rem;
    }
  }
}

.product {
  background: radial-gradient(
    var(--clr-inner, lightblue),
    var(--clr-outer, lightgreen)
  );
  padding: 3em;
  border-radius: 1em;
  margin-bottom: 5em;
  text-align: center;

  &__title {
    font-size: clamp(3rem, calc(5vw + 1rem), 5.5rem);
    text-transform: uppercase;
    line-height: 1;
    color: #fff;
    text-shadow: 0 0 0.2em rgba(#000, 0.2);
  }

  &__image {
    margin: -5em 0 0 0;
  }

  @media (min-width: 45em) {
    text-align: left;
    margin-bottom: 9em;

    &__title {
      margin: 0;
    }

    &__image {
      float: right;
      width: 65%;
      shape-outside: url(../img/shoe-1.png);
      shape-margin: 1em;
      margin: 0 -5em 0 0;
    }

    &.shoe-left {
      .product__image {
        float: left;
        shape-outside: url(../img/shoe-2.png);
        margin: 0 0 0 -5em;
        shape-margin: 2em;
      }
    }
  }
}

.hero {
  --clr-accent: #faa700;
}

.shoe-red {
  --clr-inner: #faa700;
  --clr-outer: #e48d00;
  --clr-accent: #a1173c;
}

.shoe-white {
  --clr-inner: #fce4b3;
  --clr-outer: #eac886;
  --clr-accent: #2f4858;
}

.shoe-blue {
  --clr-inner: #6dd5ed;
  --clr-outer: #2193b0;
  --clr-accent: #008951;
}
